 .blog-section {
     /* The section becomes a full-area container with a stable background. */
     padding: 40px 6px;
     background: linear-gradient(180deg, #fdf8ef 0%, #faf5e8 50%, #f8f3e5 100%);
     /* The background should stay visually fixed while content panels scroll */
     background-attachment: fixed;
     position: relative;
     overflow: visible;
 }

/* Parallax utility: apply this class to any section or div that has a
   background-image to keep the background fixed while content scrolls.
   Note: mobile browsers sometimes ignore background-attachment: fixed,
   so we fall back to scroll on small viewports. */
.parallax-fixed {
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

/* Mobile fallback: many mobile browsers don't support fixed backgrounds well.
   On small viewports we switch to scroll to avoid rendering issues. */
@media (max-width: 768px) {
    .parallax-fixed {
        background-attachment: scroll;
    }
}

/* Use the root (viewport) as the scroll-snap container so we don't create
   an inner scrolling pane (that produced the extra scrollbar). This keeps
   only the main page scrollbar and provides a cleaner, more professional look. */
html, body {
    scroll-behavior: smooth;
    scroll-snap-type: y proximity;
    /* Ensure snapped items are positioned below the sticky header */
    scroll-padding-top: var(--header-height, 80px);
}
html, body {
    /* Smooth scrolling and strong vertical snap behaviour */
    scroll-behavior: smooth;
    /* Use mandatory so the browser always snaps to the nearest snap point */
    scroll-snap-type: y mandatory;
    /* Ensure snapped items are positioned below the sticky header */
    scroll-padding-top: var(--header-height, 80px);
    /* Make sure root fills screen so 100vh calculations inside components work */
    height: 100%;
    overscroll-behavior-y: contain;
    -webkit-overflow-scrolling: touch; /* smooth momentum on iOS */
}

.blog-container {
    max-width: 1600px;
    margin: 0 auto;
}

.section-header {
    text-align: center;
    max-width: 700px;
    margin: 0 auto 50px;
}

.section-tag {
    display: inline-block;
    padding: 8px 20px;
    background: linear-gradient(135deg, #8b6914 0%, #c9a227 100%);
    color: #fff;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
    border-radius: 30px;
    margin-bottom: 20px;
}

.section-title {
    font-family: 'Playfair Display', serif;
    font-size: clamp(32px, 5vw, 48px);
    font-weight: 600;
    color: #1a1a1a;
    margin-bottom: 16px;
}

.section-desc {
    font-size: 17px;
    color: #666;
    line-height: 1.7;
}

.category-filter {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-bottom: 50px;
    padding: 0 20px;
}

.filter-btn {
    padding: 10px 20px;
    border: 2px solid #e0e0e0;
    background: #fff;
    border-radius: 30px;
    font-size: 14px;
    font-weight: 500;
    color: #666;
    cursor: pointer;
    transition: all 0.3s ease;
}

.filter-btn:hover {
    border-color: #8b6914;
    color: #8b6914;
}

.filter-btn.active {
    background: #8b6914;
    border-color: #8b6914;
    color: #fff;
}

.blog-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 36px;
}

.fresh-heading {
    font-family: 'Playfair Display', serif;
    font-size: 120px;
    font-weight: 700;
    letter-spacing: -4px;
    text-align: center;
    color: #111;
    margin: 20px 0 40px;
}
.fresh-heading {
    font-family: 'Playfair Display', serif;
    font-size: 120px;
    font-weight: 700;
    letter-spacing: -4px;
    text-align: center;
    color: #111;
    margin: 20px 0 40px;
    /* Keep the FRESH POSTS heading visible while the user scrolls the list */
    position: sticky;
    top: calc(var(--header-height, 80px) + 18px);
    z-index: 30;
    background: transparent;
    pointer-events: none; /* don't block article interactions */
}

.blog-list {
    /* Allow articles to take more horizontal space for a professional layout */
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 12px;
}

/* Snap container wraps all articles; each child snaps to center/top.
   We subtract the header height so the section panel sits below the sticky header. */
.snap-container {
    /* No inner scrolling. The page (root) is the scroll container now.
       Keep the container for semantics/layout but allow body scrolling. */
    display: block;
    height: auto;
}
.snap-container {
    /* No inner scrolling: the main page is the scroll container. */
    display: block;
    height: auto;
}


.snap-item {
    scroll-snap-align: start;
    /* Each snap item should fill the viewport below the header */
    min-height: calc(100vh - var(--header-height, 80px));
    display: flex;
    align-items: center; /* vertically center the article content */
    justify-content: center;
    /* reduce vertical padding so the content area appears larger */
    padding: 20px 10px;
    box-sizing: border-box;
    transition: transform 500ms cubic-bezier(.2,.9,.2,1), opacity 350ms ease;
}
.snap-item {
    /* required by the user: each article aligns at the start */
    scroll-snap-align: start;
    /* Prevent the browser from skipping snap points */
    scroll-snap-stop: always;
    /* Ensure the snap item exactly fills the viewport area under header */
    height: calc(100vh - var(--header-height, 80px));
    min-height: calc(100vh - var(--header-height, 80px));
    max-height: calc(100vh - var(--header-height, 80px));
    display: flex;
    align-items: center; /* vertically center the article content */
    justify-content: center;
    overflow: hidden;
    padding: 20px 10px;
    box-sizing: border-box;
    transition: transform 500ms cubic-bezier(.2,.9,.2,1), opacity 350ms ease;
}

/* Slight parallax effect: when the snap item is not in focus, scale down a little */
.snap-item:not(:target):not(:first-child) {
    /* fallback for browsers that don't support :has or JS selection */
    transform: scale(0.995);
    opacity: 0.98;
}

/* Make sure nested .post-row expands to use available width when inside snap */
.snap-item .post-row {
    width: 100%;
    /* increase the max width so content uses more of the page */
    max-width: 1400px;
}

.load-more-wrapper {
    text-align: center;
    margin-top: 60px;
}

.load-more-btn {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    padding: 16px 40px;
    background: transparent;
    border: 2px solid #1a1a1a;
    border-radius: 50px;
    font-size: 15px;
    font-weight: 600;
    color: #1a1a1a;
    cursor: pointer;
    transition: all 0.3s ease;
}

.load-more-btn:hover {
    background: #1a1a1a;
    color: #fff;
}

.load-more-btn svg {
    transition: transform 0.3s ease;
}

.load-more-btn:hover svg {
    transform: translateY(4px);
}

@media (max-width: 1100px) {
    .blog-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 768px) {
    .blog-section {
        padding: 60px 16px;
    }

    .blog-grid {
        grid-template-columns: 1fr;
        gap: 24px;
    }

    .category-filter {
        gap: 8px;
    }

    .filter-btn {
        padding: 8px 16px;
        font-size: 13px;
    }
}

/* Scroll Animation Styles */
.scroll-animate {
    opacity: 0;
    transform: translateY(40px);
    transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.scroll-animate.visible {
    opacity: 1;
    transform: translateY(0);
}

.scroll-animate-delay-1 {
    transition-delay: 0.1s;
}

.scroll-animate-delay-2 {
    transition-delay: 0.2s;
}

.scroll-animate-delay-3 {
    transition-delay: 0.3s;
}

.scroll-animate-delay-4 {
    transition-delay: 0.4s;
}

.scroll-animate-delay-5 {
    transition-delay: 0.5s;
}
